@import url("https://fonts.googleapis.com/css2?family=Playpen+Sans:wght@100..800&display=swap");

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

@media only screen and (max-width: 31.25em) {
  html {
    font-size: 50%;
  }
}

:root {
  --color-grey-dark-0: #495057;
  --color-grey-light-0: #f1f3f5;
  --color-grey-light-1: #e9ecef;

  --color-primary: #ae3ec9;
}

body {
  font-family: "Playpen Sans", cursive;
  font-size: 1.6rem;
  line-height: 1.6;
  font-weight: 400;
  color: var(--color-grey-dark-0);
}

.app {
  display: grid;
  grid-template-columns:
    [full-start left-start] minmax(6rem, 1fr) [left-end center-start] repeat(
      10,
      [col-start] minmax(min-content, 4rem) [col-end]
    )
    [center-end right-start] minmax(6rem, 1fr) [right-end full-end];
}

.counter {
  grid-column: center-start/center-end;

  margin: 5rem 0;
  padding: 3rem 5rem;
  background-color: var(--color-grey-light-1);
  border-radius: 9px;

  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.row-count {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.row-count div {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.row-step {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.row-text {
  text-align: center;
  margin-top: 2rem;
}

.btn {
  border: none;
  cursor: pointer;
  height: 4rem;
  width: 6rem;
  color: var(--color-grey-light-0);
  background-color: var(--color-primary);
  font-weight: 600;
  font-family: inherit;
  font-size: 2.5rem;
  border-radius: 9px;
}

.row-count input {
  border: none;
  font-family: inherit;
  padding: 0.9rem 1rem;
  font-size: 1.5rem;
  width: 60%;
}

.row-count input:focus {
  outline: 2px solid var(--color-primary);
}

.row-text span {
  border-bottom: 2px solid var(--color-primary);
}

.row-reset {
  margin-top: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row-reset .btn {
  font-size: 1.4rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0rem 2rem;
  width: auto;
}
